
body,
html {
  height: 100%;
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
#app {
  width: 10rem;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
#mask {
  width: 10rem;
  height: 100%;
  min-height: 17.777778rem;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 103;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.6);
}
.loading {
  position: fixed;
  top: 45%;
  margin-left: 3.148148rem;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.slide-enter-active {
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.slide-leave-active {
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.slide-enter,
.slide-leave-to {
  -webkit-transform: translateX(80%);
          transform: translateX(80%);
}


