
.toast {
  position: fixed;
  left: 50%;
  -webkit-transform: translate(-50%,-50%) scale(1);
          transform: translate(-50%,-50%) scale(1);
  word-wrap: break-word;
  padding: 0.092593rem;
  text-align: center;
  z-index: 9999;
  font-size: 0.333333rem;
  max-width: 80%;
  color: #fff;
  border-radius: 0.092593rem;
  padding: 0.240741rem;
  background: rgba(0,0,0,0.7);
  overflow: hidden;
}
.toast.middle {
  top: 50%;
}
.toast.middleBottom {
  top: 58%;
}
.toast.top {
  top: 10%;
}
.toast.bottom {
  top: 90%;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}


